<template>
  <section>
    <img :src="image" class="bg">

    <div class="content">
      <div :style="{background:'url('+image+') no-repeat'}">
        <input type="text" v-model="inputTmp" @keyup.enter="changeCity" placeholder="搜索城市"/>
        <span class="search-icon" @click="changeCity">
        <svg class="icon" aria-hidden="true">
          <use xlink:href="#icon-search"></use>
        </svg>
        </span>
        <div class="weizhi">
          <svg class="icon" aria-hidden="true">
            <use xlink:href="#icon-weizhi"></use>
          </svg>
          <p>{{city}}</p>
          <p>{{now}}</p>
        </div>
      </div>
      <div class="weather">
        <div class="weather-today">
          <p class="temp">{{cur.realTime}}<span>°</span></p>
          <div :class="weatherToIcon(cur.weather)">
            <svg class="icon" aria-hidden="true">
              <use :xlink:href="'#icon-'+ weatherToIcon(cur.weather)"></use>
            </svg>
          </div>
          <p class="temp2"><span class="date">{{cur.date}}</span>{{cur.h}}°&nbsp;/&nbsp;{{cur.l}}°&nbsp;&nbsp;{{cur.weather}}</p>
        </div>
        <ul>
          <li v-for="f in future">
            <p><span class="date">{{f.week}}</span></p>
            <p> <svg class="icon" aria-hidden="true">
              <use :xlink:href="'#icon-'+ weatherToIcon(f.weather)"></use>
            </svg></p>
            <p>{{f.temp_high}}°/{{f.temp_low}}</p>
          </li>
        </ul>
      </div>
    </div>
  </section>
</template>

<script>
  import {getCityImage, getLocation, getCurWeather, getFutureWeather} from '../api'

  export default {
    name: 'home',
    data () {
      return {
        image: '',
        inputTmp: '',
        city: '上海',
        now: '',
        cur: {
          date: '',
          realTime: '',
          h: '',
          l: '',
          weather: ''
        },
        future: []
      }
    },
    methods: {
      async getCityImage () {
        let res = await getCityImage(this.city)
        res = JSON.parse(res.substring(res.indexOf('(') + 1, res.length - 1))
        let photo = res.photos.photo[0]
        this.image = `https://farm${photo.farm}.staticflickr.com/${photo.server}/${photo.id}_${photo.secret}_b.jpg`
      },
      async getCurWeather () {
        let res = await getCurWeather(this.city)
        console.log(res)
        let date = res.results[0].weather_data[0].date
        this.cur.date = date.substr(0, 9)
        this.cur.realTime = date.substr(14, 2)
        let temp = res.results[0].weather_data[0].temperature
        let i = temp.indexOf(' ~ ')
        this.cur.h = temp.substring(0, i)
        this.cur.l = temp.substring(i + 3, temp.length - 1)
        this.cur.weather = res.results[0].weather_data[0].weather
      },
      async getFutureWeather () {
        let res = await getFutureWeather(this.city)
        this.future = res.result
        this.future.shift()
        console.log(this.future)
      },
      weatherToIcon (weather) {
        if (weather.indexOf('雾') > -1) {
          return 'weatherfog'
        }
        if (weather.indexOf('雷') > -1) {
          return 'weatherstormy'
        }
        if (weather.indexOf('雪') > -1) {
          return 'weathersnow'
        }
        if (weather.indexOf('雨') > -1) {
          return 'weatherrain'
        }
        if (weather.indexOf('阴') > -1) {
          return 'weather1'
        }
        if (weather.indexOf('多云') > -1) {
          return 'weather'
        }
        if (weather.indexOf('晴') > -1) {
          return 'sun'
        }
      },
      changeCity () {
        if (!this.inputTmp) { return }
        this.city = this.inputTmp
        this.getCityImage()
        this.getCurWeather()
        this.getFutureWeather()
      },
      displayTime () {
        this.now = new Date().toLocaleTimeString()
        setTimeout(this.displayTime, 1000)
      }
    },
    async created () {
      this.displayTime()
      let res = await getLocation()
      this.city = res.content.address_detail.city
      this.city = this.city.replace('市', '')
      this.getCityImage()
      this.getCurWeather()
      this.getFutureWeather()
    }
  }
</script>

<style scoped lang="less">
  section {
    display: flex;
    justify-content: center;
    align-items: center;
  }

  img.bg {
    min-height: 100%;
    min-width: 1024px;
    width: 100%;
    height: auto;
    position: fixed;
    top: 0;
    left: 0;
    -webkit-filter: blur(30px) opacity(.8);
    filter: blur(30px opacity(.8));
    background: #45C5C3;
  }

  @media screen and (max-width: 1024px) {
    img.bg {
      left: 50%;
      margin-left: -512px;
    }
  }

  .content {
    width: 1024px;
    height: 650px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    z-index: 1;
    > div:nth-of-type(1) {
      width: 1024px;
      height: 70%;
      background-size: cover;
      border-top-left-radius: 15px;
      border-top-right-radius: 15px;
      > .weizhi {
        float: right;
        margin-right: 30px;
        margin-top: 30px;
        > svg {
          width: 40px;
          height: 40px;
        }
        > p {
          color: #fff;
        }
      }

    }
    > div:nth-of-type(2) {
      width: 1024px;
      height: 30%;
      background: rgba(255, 255, 255, .1);
      border-bottom-left-radius: 15px;
      border-bottom-right-radius: 15px;
      display: flex;
      flex-direction: row;
      justify-content: center;
      .temp {
        color: #fff;
        font-size: 120px;
        display: inline-block;
        > span {
          font-size: 50px;
          position: absolute;
          margin-top: 18px;
        }
        margin-right: 20px;
      }
      > ul {
        height: 100%;
        display: inline-block;
        flex-direction: row;
        justify-content: center;
        > li {
          height: 100%;
          display: inline-block;
          border-left: 1px solid rgba(0, 0, 0, .2);
          box-sizing: border-box;
          color: #fff;
          >p{
            box-sizing: border-box;
            padding-top: 20px;
            svg{
              width: 50px;
              height: 50px;
            }
          }

        }
      }
      &.weather {
        > div {
          width: 40%;
          height: 100%;
        }
        > ul {
          width: 60%;
          > li {
            width: 16%;
          }
        }
        > .weather-today {
          .sun {
            line-height: 0;
          }
          svg {
            width: 100px;
            height: 100px;
          }
          .temp2 {
            color: #fff;
            margin-top: -20px;
            > span {
              margin-right: 30px;
            }
          }
          >div{
            display: inline-block;
          }
        }
      }
    }
    input {
      background-color: transparent;
      color: #fff;
      height: 50px;
      width: 300px;
      margin-top: 30px;
      border: 1px solid rgba(255, 255, 255, .2);
      padding-left: 20px;
    }
    .search-icon {
      display: inline-block;
      width: 50px;
      height: 50px;
      margin-left: -50px;
      position: absolute;
      margin-top: 30px;
      line-height: 50px;
      cursor: pointer;
    }
    .date{
      display: inline-block;
      background: rgba(0,0,0,.1);
      padding: 5px 15px;
      border-radius: 15px;
    }
  }
</style>



