import axios from 'axios'
import fetchJsonp from 'fetch-jsonp'

axios.interceptors.response.use(response => {
  return response.data
}, error => {
  return Promise.reject(error.response.data)
})
export const getCityImage = (city) => axios.get(`https://api.flickr.com/services/rest?sort=interestingness-desc&text=${city}&method=flickr.photos.search&api_key=4c9b81f53b3fb811eac0f68c8490050a&format=json`)

export const getLocation = () => fetchJsonp(`http://api.map.baidu.com/location/ip?ak=GerWvrKTTG5M1YDdAxIxCS6A`)
  .then(response => response.json())
  .then(data => Promise.resolve(data))

export const getCurWeather = (city) => fetchJsonp(`http://api.map.baidu.com/telematics/v3/weather?output=json&ak=GerWvrKTTG5M1YDdAxIxCS6A&location=${city}`)
  .then(response => response.json())
  .then(data => Promise.resolve(data))

export const getFutureWeather = (city) => fetchJsonp(`http://sapi.k780.com/?app=weather.future&appkey=10003&sign=b59bc3ef6191eb9f747dd4e83c99f2a4&format=json&weaid=${city}`,
  {
    jsonpCallback: 'jsoncallback'
  })
  .then(response => response.json())
  .then(data => Promise.resolve(data))



